.div {
    display: flex;
    justify-content: space-between;
    width: 440px;
    margin: 20px 60px;
    padding: 5px 10px 10px 10px;
    background-color: #ddff99;
}
.deleteButton {
background:#dc2a9f;
    color: #ddff99;
    margin: 10px;
    width: 30px;
}

.filterButton {
    background:#708e26;
    margin-left: 60px;
    width: 70px;
    color: #ddff99;
}
.text {
    color: #708e26;
    word-wrap: break-word;
}

.item{
    margin: 10px;
    width: 100px;
    word-wrap: break-word;
}
.high{
    color: hotpink;
}
.low{
    color: #708e26;
}
.middle{
    color: darkorange;
}
